// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import VueRouter from 'vue-router'
import VueResource from 'vue-resource'
import Header from "./components/header/header.vue"
import Destination from "./components/destination/destination.vue"
import Shop from "./components/shop/shop.vue"
import Person from "./components/person/person.vue"
import Goods from "./components/goods/good.vue"
import Favourite from "./components/myfavourite/myfavourite.vue"
import Place from "./components/myplace/myplace.vue"
import "./common/fonts/iconfont.css"
Vue.use(VueRouter);
Vue.use(VueResource);
let router = new VueRouter({
  mode: 'history',
  linkActiveClass: 'active',
  routes: [
    { path: "/", redirect: "/goods" },
    { path: "/goods", component: Goods },
    { path: "/destination", component: Destination },
    { path: "/shop", component: Shop },
    {
      path: "/person",
      component: Person,
      children: [
        { path: "/person", redirect: "/person/favourite" },
        { path: "/person/favourite", component: Favourite },
        { path: "/person/place", component: Place }
      ]
    }
  ]
})

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  template: '<App/>',
  components: { App }
})



