<template>
  <div class="footer">
    <div class="footer__favourite common" @click="addFavourite">
      <i class="iconfont" :class="[show?'icon-zan1':'icon-zan']"></i>
      <span>{{favrourite}}</span>
    </div>
    <div class="footer__comment common" @click="getcomment">
      <i class="iconfont icon-pinglun1"></i>
      <span>评论({{count}})</span>
    </div>
    <div class="footer__share common" @click="collects">
      <i class="iconfont" :class="[hide?'icon-shoucang1':'icon-shoucang']"></i>
      <span>{{collect}}</span>
    </div>
    <!-- 蒙版 -->
    <div class="footer__mosk" v-show="mosk">
      <div class="footer__mosk-text">
        {{prompt}}
      </div>
    </div>
  </div>
</template>
<script type="text/javascript">
import bus from "../../../static/eventBus.js"
var contrastdata = JSON.parse(localStorage.getItem('selects'));
if (contrastdata) {
  contrastdata = {}
}
export default {
  props: ["count", "select"],
  data() {
    return {
      show: false,
      hide: false,
      mosk: false,
      localDate: {},
      localArr: [],
      // contrastdata: {},
      nums: 0
    }
  },
  // created() {
  //   this.getdata()
  // },
  updated() {
    this.getlocal()
  },
  methods: {
    addFavourite() {
      this.show = !this.show;
      console.log(this.select)
    },
    // getdata() {
    //   this.contrastdata = JSON.parse(localStorage.getItem('selects'));
    //   console.log(this.contrastdata)
    //   if (this.contrastdata) {
    //     this.contrastdata = {}
    //   }
    // },
    getlocal() {
      localStorage.setItem('selects', JSON.stringify(this.localArr));
    },
    collects() {
      this.hide = !this.hide;
      this.mosk = !this.mosk;
      // 本地存储 之后在我的收藏里面展示
      this.localDate.title = this.select.title;
      this.localDate.pic = this.select.pic;
      this.localDate.ptag = this.select.ptag;
      this.localArr.push(this.localDate);
      // let selects = "select" + (++this.nums);
      // localStorage.setItem("selects", JSON.stringify(this.localArr));
      localStorage.setItem('sel', 1);

      // bus.$emit("getdata", "我是传输的数据");
      setTimeout(() => {
        this.mosk = false
      }, 1000)
    },
    getcomment() {

    }
  },
  computed: {
    favrourite() {
      if (this.show === true) {
        return "赞"
      } else {
        return "已藏"
      }
      return favrourite
    },
    collect() {
      if (this.hide === true) {
        return "收藏"
      } else {
        return "未收藏"
      }
      return collect
    },
    prompt() {
      if (this.hide === true) {
        return " 请在我的收藏里面查看"
      } else {
        return "取消收藏"
      }
      return prompt
    }
  }
}

</script>
<style lang="less">
.footer {
  position: fixed;
  bottom: 0;
  left: 0;
  display: flex;
  justify-content: space-around;
  height: 48px;
  line-height: 48px;
  width: 100%;
  z-index: 150;
  background: #f5f5f5;
  border-top: 1px solid rgba(7, 17, 27, 0.2);
  .common {
    flex: 1;
    text-align: center;
    color: #3c9;
  }
  .iconfont {
    font-size: 24px;
  }
  &__mosk {
    position: fixed;
    top: 65%;
    left: 30%;
    bottom: 48px;
    text-align: center;
    height: 34px;
    color: #fff;
    background: rgba(0, 0, 0, 0.6);
    font-size: 14px;
    padding: 5px 10px;
    box-sizing: border-box;
    line-height: 22px;
    z-index: -1;
    border-radius: 5px;
  }
}

.on {
  color: #3c9;
}

</style>



